*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
/*
rgb(255, 132, 0);
#09ba09;
*/

.NavBar{    
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px;
  background: #fff;
  box-shadow: 2px 3px 3px rgb(196, 196, 196);
}

.logo{
  height: auto;
  width: 10em;
  cursor: pointer;
}

.logo:hover {
  transform: scale(1.1);
  transition: 0.8s;
}

@media screen and (max-width: 768px) {
  .menu {
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }

  .menu a {
    flex-basis: auto;
  }
}

.menu{
    display: flex;
    gap: 70px;
}

.menu a{
    display: flex;
      flex-grow: 1;
      align-items: center;
      font-size: 14px;
      font-weight: 500;
      text-decoration: none;
      font-family: roboto, sans-serif;
      color: #09ba09;
    }

@media screen and (max-width: 460px) {
  .menu {
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }

  .menu a {
    flex-basis: auto;
  }
}

.menu a:hover {
  transform: scale(1.3);
  color: rgb(255, 132, 0);
  transition: 0.5s;
}

.Option {
  display: flex;
  margin: 0 10px;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  font-family: roboto, sans-serif;
  color: #09ba09;
}

.Option:hover {
  transform: scale(1.3);
  color: rgb(255, 132, 0);
  transition: 0.5s;
}

.ActiveOption {
  display: flex;
  align-items: center;
  font-size: 18px;
  text-decoration: none;
  font-family: roboto, sans-serif;
  color: rgb(255, 132, 0);
}

.LogIn {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
}

.LogIn:hover{
  transform: scale(1.2);
transition: 0.5s;
}
.LogIn a{
  color: white;
}

.LogIn button{
  border-radius: 10px;
  background-color: rgb(255, 132, 0);
  text-decoration: none;
  padding: 10%;
  border: none;
}
